
.main {
  background-image: linear-gradient(lightblue,lightpink,lightblue);
  height:95vh;
  width:99vw;
}
.nav {
  list-style-type: none;
  display:flex;
  justify-content: space-around;
  background-color: rgb(4, 4, 69);
  color:white;
  font-size: 25px;
  font-style: italic;
  cursor:pointer;
  height:42px;
  padding-top: 8px;
}
.item {
  transition: all 1s linear 0s;
  text-decoration: none;
  color:white;
}
.item:hover {
  color:lightcoral;
}
.home,.contact{
  font-weight: bolder;
  font-size:25px;
  text-align:center;
}
.upper-section {
  display: flex;
  justify-content: space-around;
}
.btn {
  font-size:22px;
  border-radius: 30px;
  height:42px;
  width:200px;

}
.table {
  border:1px solid black;
  text-align: center;
  width:90%;
  margin:auto;
  background-color:white;
  padding:20px;
  border-collapse: collapse;

}
td {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}